module.exports = "import { Component } from '@angular/core';\r\n\r\n@Component({\r\n  selector: 'nz-demo-date-picker-mode',\r\n  template: `\r\n    <nz-date-picker\r\n      [nzMode]=\"dateMode\"\r\n      nzShowTime\r\n      (nzOnOpenChange)=\"handleDateOpenChange($event)\"\r\n      (nzOnPanelChange)=\"handleDatePanelChange($event)\">\r\n    </nz-date-picker>\r\n  `,\r\n  styles: [`\r\n    nz-date-picker, nz-month-picker, nz-range-picker, nz-week-picker {\r\n      margin: 0 8px 12px 0;\r\n    }\r\n  `]\r\n})\r\n\r\nexport class NzDemoDatePickerModeComponent {\r\n  dateMode = 'time';\r\n\r\n  handleDateOpenChange(open: boolean): void {\r\n    if (open) {\r\n      this.dateMode = 'time';\r\n    }\r\n  }\r\n\r\n  handleDatePanelChange(mode: string): void {\r\n    console.log('handleDatePanelChange: ', mode);\r\n  }\r\n}\r\n"