module.exports = "import { Component } from '@angular/core';\r\nimport * as endOfMonth from 'date-fns/end_of_month';\r\n\r\n@Component({\r\n  selector: 'nz-demo-date-picker-presetted-ranges',\r\n  template: `\r\n    <nz-range-picker\r\n      [nzRanges]=\"ranges1\"\r\n      ngModel\r\n      (ngModelChange)=\"onChange($event)\"\r\n    ></nz-range-picker>\r\n    <br>\r\n    <nz-range-picker\r\n      [nzRanges]=\"ranges1\"\r\n      nzShowTime\r\n      nzFormat=\"yyyy/MM/dd HH:mm:ss\"\r\n      ngModel\r\n      (ngModelChange)=\"onChange($event)\"\r\n    ></nz-range-picker>\r\n  `,\r\n  styles  : [ `\r\n    nz-date-picker, nz-month-picker, nz-range-picker, nz-week-picker {\r\n      margin: 0 8px 12px 0;\r\n    }\r\n  ` ]\r\n})\r\n\r\nexport class NzDemoDatePickerPresettedRangesComponent {\r\n  ranges1 = { 'Today': [ new Date(), new Date() ], 'This Month': [ new Date(), endOfMonth(new Date()) ] };\r\n  ranges2 = { 'Today': [ new Date(), new Date() ], 'This Month': [ new Date(), endOfMonth(new Date()) ] };\r\n\r\n  onChange(result: Date[]): void {\r\n    console.log('From: ', result[ 0 ], ', to: ', result[ 1 ]);\r\n  }\r\n}\r\n"